import React from 'react';
import { withRouter } from 'react-router-dom';
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Link } from 'react-router-dom';
import axios from 'axios';

class Posters extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: []
        }
    }

    componentWillMount = () => {
        this.getMovies();
    }

    getMovies = () => {
        axios.get("http://localhost:65332/api/poster/getall")
            .then(resp => {
                let movies = resp.data.items;
                this.setState({ movies })
                console.log(resp);
                console.log(movies);
            });
    }

    render() {

        return (
           
            <div className="container">
            <div className="container">
            <h2 className="movietitlefont">MoviePosters</h2>
                </div>
                <div className="row">
                {this.state.movies.map((movie) =>
                    <div key={movie.id}>
                    
                    <div className="row col">
                    <img className="row ibox md-3 movies" src={movie.imageUrl} href="" />
                        <div>{movie.title}</div>
                        <div>{movie.description}</div>
                        <div>{movie.type}</div>
                        <div>{movie.price}</div>
                        </div>                       
                    </div>
                    
                )}
</div>
            </div>
        );
    }
}
export default Posters;


